<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <router-link to="/test">
        test
    </router-link>

    <a href="#" @click="asd" id="qqqqqq">
      aaasdasdasd
    </a>

  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your BaseApp'
    }
  },
  methods: {
    asd(){
      console.log('asdasd');
      $('#qqqqqq').hide()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.hello {
  padding:200px 0px;
}
</style>



